import "./App.css";
import { Component } from "react";
// import NewLoader from "./components/Loader";
import Searchbar from "./components/Searchbar/Searchbar";
import ImageGallery from "./components/ImageGallery";
const BASE_URL = "https://pixabay.com/api";
const API_KEY = "23984198-d5ba81eacae48fb09d6c38179";
class App extends Component {
  state = {
    gallery: [],
  };

  componentDidMount() {
    fetch(
      `${BASE_URL}/?q=forest&page=1&key=${API_KEY}&image_type=photo&orientation=horizontal&per_page=12`
    )
      .then((response) => response.json())
      .then(({ hits }) => this.setState({ gallery: hits }));
  }
  render() {
    const { gallery } = this.state;
    return (
      <div className="App">
        <Searchbar />
        <ImageGallery data={gallery} />
        {/* <NewLoader /> */}
      </div>
    );
  }
}

export default App;
